<template>
  <div>
    <el-button style="margin-bottom:20px;" type="primary" size="mini" @click="$router.push('/')">返回</el-button>
    <el-form :model="addClassForm">
      <el-form-item>
        <el-input placeholder="输入课程名称" v-model="addClassForm.className" />
      </el-form-item>
      <el-form-item>
        <el-input placeholder="输入授课老师姓名" v-model="addClassForm.teacherName" />
      </el-form-item>
      <el-form-item>
        <div style="display:flex">
          <el-select v-model="addClassForm.timeValue" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <el-select v-model="addClassForm.timeValue1" placeholder="请选择">
            <el-option
              v-for="item in options1"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <span>至</span>
          <el-select v-model="addClassForm.timeValue2" placeholder="请选择">
            <el-option
              v-for="item in options2"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </div>
      </el-form-item>
      <el-form-item>
        <el-input placeholder="上课地点" v-model="addClassForm.classRoom" />
      </el-form-item>
      <el-form-item style="text-align: center">
        <el-button @click="handleAddSure" type="primary">添加课程</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'addClass',
  data() {
    return {
      addClassForm: {
        className: '',
        teacherName: '',
        classRoom: '',
        timeValue: '',
        timeValue1: '',
        timeValue2: ''
      },
      options: [
        {
          value: 'monday',
          label: '周一'
        },
        {
          value: 'tuesday',
          label: '周二'
        },
        {
          value: 'wednesday',
          label: '周三'
        },
                {
          value: 'thursday',
          label: '周四'
        },
        {
          value: 'friday',
          label: '周五'
        },
        {
          value: 'saturday',
          label: '周六'
        },
        {
          value: 'sunday',
          label: '周日'
        },
      ],
      options1: [
        {
          value: '1',
          label: '第一节'
        },
        {
          value: '2',
          label: '第二节'
        },
        {
          value: '3',
          label: '第三节'
        },
        {
          value: '4',
          label: '第四节'
        },
        {
          value: '5',
          label: '第五节'
        },
        {
          value: '6',
          label: '第六节'
        }, 
        {
          value: '7',
          label: '第七节'
        },
        {
          value: '8',
          label: '第八节'
        },
        {
          value: '9',
          label: '第九节'
        }, 
        {
          value: '10',
          label: '第十节'
        },
        {
          value: '11',
          label: '第11节'
        },
        {
          value: '12',
          label: '第12节'
        }                                        
      ],
      options2: [
        {
          value: '1',
          label: '第一节'
        },
        {
          value: '2',
          label: '第二节'
        },
        {
          value: '3',
          label: '第三节'
        },
        {
          value: '4',
          label: '第四节'
        },
        {
          value: '5',
          label: '第五节'
        },
        {
          value: '6',
          label: '第六节'
        }, 
        {
          value: '7',
          label: '第七节'
        },
        {
          value: '8',
          label: '第八节'
        },
        {
          value: '9',
          label: '第九节'
        }, 
        {
          value: '10',
          label: '第十节'
        },
        {
          value: '11',
          label: '第11节'
        },
        {
          value: '12',
          label: '第12节'
        }                                        
      ],      
    }
  },
  methods: {
    // 添加课程的按钮点击事件
    handleAddSure(){
      console.log('对象', this.addClassForm)
      this.$store.commit('changeData', this.addClassForm)
      this.$confirm('成功添加课表, 是否继续添加?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'success'
      }).then(() => {
        this.addClassForm = {
          className: '',
          teacherName: '',
          classRoom: '',
          timeValue: '',
          timeValue1: '',
          timeValue2: ''
        }
      }).catch(() => {
        this.$router.push('/')         
      });
    }
  },
}
</script>

<style scoped>
ul,li{
  list-style: none;
}
</style>


