import Vue from 'vue'
import Router from 'vue-router'
import Table from '@/components/table'
import addClass from '@/components/addClass'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Table',
      component: Table
    },
    {
      path: '/addClass',
      name: 'addClass',
      component: addClass
    }
  ]
})



